.navigation__logo {
  position: absolute;
  top: 316px;
  left: 64px; }

.navigation__toggles {
  display: none; }

.navigation__close,
.navigation__open,
.navigation__toggle {
  border: none;
  background-color: inherit; }

.navigation__toggle-open {
  background-color: #0AD9C6;
