  padding-top: 21px; }

.navigation__item {
  white-space: nowrap; }

.navigation__button:hover,
.navigation__button:focus {
  color: #06877B; }

.navigation__destinations::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: -19px;
  right: -20px;
  max-width: 930px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.navigation__button {
  border: none;
  background-color: inherit;
  color: #ffffff;
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  opacity: 0.7;
  padding-right: 0;
  padding-left: 0;
  cursor: pointer; }

.navigation__item:not(:last-child) {
  padding-right: 135px; }

.navigation__buttons {
  position: relative; }

@media (max-width: 1023px) {
  .navigation__inner {
    width: 100%; }
  .navigation__logo {
    position: relative;
    top: 9px;
    left: 8px;
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px; }
  .navigation__toggles {
    display: block;
    z-index: 2; }
  .navigation__logo,
  .navigation__destinations,
  .navigation__buttons {
    width: 100%; }
  .navigation__buttons {
    display: flex; }
  .navigation__destinations {
    position: relative;
    flex-direction: column;
    display: flex;
    left: 0;
    transform: translateX(0);
    max-width: 1023px;
    height: 100vh;
    overflow: scroll; }
  .navigation__item {
    background-color: #F6F6F6;
    margin-bottom: 30px;
    padding: 15px 0 15px 15px;
    position: relative; }
  .navigation__item::after {
    content: "";
    position: absolute;
    right: 20px;
    top: 25px;
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    background-image: url(../img/icon-arrow.svg); }
  .navigation__item:not(:last-child) {
    padding-right: 0; }
  .navigation__button {
    color: #48494D;
    font-size: 20px;
    line-height: 28px;
    font-weight: 400; }
  .container {
    margin: 0;
    padding: 0; }
  .navigation__destinations::after {
    content: none; }
  .navigation__close {
    display: block; }
  .navigation__toggle-close {
    padding-top: 10px;
    padding-right: 30px; }
  .hidden {
    display: none; } }

@media (max-width: 767px) {
  .navigation__logo {
    top: 10px;
    left: 4px; }
  .navigation__destionations {
    max-width: 767px; } }

